<template>
  <div class="notchedBar">
    <div v-for="i in Number(max)" :key="i" class="notchBox" :class="{notchInactive: i>modelValue}" @click="handleClick(i)">
      <i class="bi-check"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    'modelValue': [String, Number],
    'max': {
      default: 5,
      type: [String, Number],
    },
  },
  methods: {
    handleClick(i) {
      if (this.modelValue == i) {
        this.$emit('update:modelValue', i-1)
      } else {
        this.$emit('update:modelValue', i)
      }
    }
  },
  emits: ['update:modelValue']
}
</script>

<style>
.notchedBar {
  min-height: 5px;
  min-width: 20px;
  display: inline-flex;
  align-items: center;
  border: 1px solid #0005;
  border-radius: 6px;
}
.notchBox:last-child {
  border-radius: 0 6px 6px 0;
}
.notchBox:first-child {
  border-radius: 6px 0 0 6px;
}
.notchBox {
  background: #444;
  height: 18px;
  width: 18px;
  display: flex;
  margin: 1px;
  border: 1px solid #0005;
}
.notchBox i {
  position: absolute;
  transform: translate(0.5px, -3px);
  color: #fff;
}
.notchInactive i {
  color: #000;
}
.notchBox:hover {
  background: #777;
}
.notchInactive {
  background: #ccc;
}
.notchInactive:hover {
  background: #ddd;
}
</style>