<template>
<div class="loadcol">
  <div v-for="(char, key) in state.characters" :key="key" class="loaderbox">
    <h6>{{ char.name }} {{ key }}</h6>
    <button @click=load_f(key)>Load</button>
    <br>
    <button @click=overwrite(key)>Overwrite</button>
    <br>
    <button @click=export_f(key)>Export</button>
    <br>
    <button @click=delete_f(key)>Delete</button>
  </div>
  <div class="loaderbox">
    <button @click=state.saveAsNew>Save as new to {{state.firstFreeIndex}}</button>
    <br>
    <button @click=import_f>Import as new</button>
    <br>
    <button @click=defaults_f>Load defaults</button>
  </div>
</div>
</template>

<script>
import {useState} from '@/stores/DndState.js'

export default {
  data() { return {
    state: useState(),
  }},
  methods: {
    load_f(slot) {
      const charname = this.state.characters[slot].name
      if (!confirm(`Load slot ${charname} from slot ${slot}?`))
        return
      this.state.load(slot)
    },
    overwrite(slot) {
      if (!confirm(`Overwrite slot ${slot}?`))
        return
      this.state.save(slot)
    },
    export_f(slot) {
      const data = this.state.characters[slot]
      const strData = JSON.stringify(data)
      navigator.clipboard.writeText(strData).then(() => {
        alert("Worked")
      })
    },
    import_f() {
      navigator.clipboard.readText().then((data) => {
        alert(data)
        this.state.characters[this.state.firstFreeIndex] = JSON.parse(data)
        localStorage.setItem("characters", JSON.stringify(this.state.characters))
      })
    },
    delete_f(slot) {
      const charname = this.state.characters[slot].name
      if (!confirm(`DELETE ${charname} from slot ${slot}?`))
        return
      this.state.delete(slot)
    },
    defaults_f() {      
      if (!confirm(`Load slot default values?`))
        return
      this.state.reset()
    },
  }
}
</script>

<style scoped>
.column {
  background: rgb(230, 230, 235);
  margin: 1px;
}
.loaderbox {
  position: relative;
  height: 160px;
  width: 200px;
  margin: 2px;
  border: 1px dotted #0005;
  border-radius: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.loadcol {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
</style>