      </div>
    </div>
    <div class="row">
      <div class="column col-lg dndcolumn">
        <DndDragColumn :number="3"></DndDragColumn>
      </div>
      <div class="column col-lg dndcolumn">
        <DndDragColumn :number="4"></DndDragColumn>
      </div>
      <div class="column col-lg dndcolumn">
        <DndDragColumn :number="5"></DndDragColumn>
      </div>
    </div>
    <div class="row">
      <div class="column col">
        <DndLoadData></DndLoadData>
      </div>
    </div>
  </div>
  <div v-if="state.dragging">
    <draggable 
      v-model="draggable_eater" 
      group="dndwidgetholder"
      item-key="id"
      class="eater"
      ghost-class="eaterGhost">
      <template #item></template>
