<script>
import {useStore} from '@/stores/DndCharacter.js'
import {useState} from '@/stores/DndState.js'
import DndInput from '@/components/DndInput.vue'
import DndDragColumn from '@/components/DndDragColumn.vue'
import DndWidgetAdder from '@/components/DndWidgetAdder.vue'
import DndLoadData from '@/components/DndLoadData.vue'
import draggable from 'vuedraggable'

export default {
  data() {return {
    store: useStore(),
    state: useState(),
    draggable_eater: [],
  }},
  components: {
    DndInput,
    DndDragColumn,
    DndWidgetAdder,
    draggable,
    DndLoadData,
  },
  watch: {
    draggable_eater() {
      // Stop recursion, stop if already empty
      if (this.draggable_eater.length <= 0)
        return
      this.draggable_eater = []
      console.log("EATEN")
      this.store.cleanElementData()
    }
  },
}
</script>
