<template>
  <div class="countercontainer">
    <input v-model="store.elementData[element.id].name" type="text" placeholder="Name" class="inputtitle handle countername">
    <NotchedBar v-model="store.elementData[element.id].value" :max="computedMax"></NotchedBar>
    <input type="number" placeholder="Max" class="inputstyle countermaxnum" v-model="store.elementData[element.id].max">
  </div>
</template>

<script>
import {useStore} from '@/stores/DndCharacter.js'
import NotchedBar from '@/components/NotchedBar.vue'

export default {
  props: {
    element: Object,
  },
  data() {return {
    store: useStore(),
  }},
  beforeMount() {
    // Prevent errors by ensuring store has an object that refers to this
    const me = this.store.elementData[this.$props.element.id]
    if (me == null) {
      console.log("THIS DOES NOT EXIST CREATING")
      this.store.elementData[this.$props.element.id] = {
        name: '',
        value: 1,
        max: 4,
      }
    }
  },
  components: {
    NotchedBar,
  },
  computed: {
    computedMax() {
      const max = this.store.elementData[this.element.id].max
      return max > 0 ? max : 1
    }
  },
}
</script>

<style scoped>
.countername {
  min-width: 0px;
  width: 10% !important;
  flex-grow: 10;
  text-align: left !important;
}
.countermaxnum {
  width: 30px;
}
.countercontainer {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin: 5px 0;
}
</style>