import { defineStore } from 'pinia'
import { useStore } from '@/stores/DndCharacter.js'

function filter(obj) {
  return Object.fromEntries(Object.entries(obj).filter(
    ([key, ]) => {
      if (key.startsWith('_')) return false
      if (key.startsWith('$')) return false
      return true
    }
  ))
}

function loadData() {
  return JSON.parse(localStorage.getItem('characters')) || {}
}

export const useState = defineStore('dndState', {
  state() {
    const character = useStore()
    const default_character = JSON.parse(JSON.stringify(filter({...character})))
    return {
      dragging: false,  
      character: character,
      defaultCharacter: default_character,
      characters: loadData(),
    }
  },
  actions: {
    reset() {
      this.character.$patch({...this.defaultCharacter})
      // Gotta unlink it again so reset can happen again
      this.defaultCharacter = JSON.parse(JSON.stringify(this.defaultCharacter))
    },
    saveAsNew() {
      this.save(this.firstFreeIndex)
    },
    save(slot) {
      this.characters[slot] = this.character
      localStorage.setItem('characters', JSON.stringify(this.characters))
    },
    load(slot) {
      this.reset()
      this.character.$patch(this.characters[slot])
    },
    delete(slot) {
      delete this.characters[slot]
      localStorage.setItem('characters', JSON.stringify(this.characters))
    },
    filter(obj) {
      return filter(obj)
    } 
  },
  getters: {
    firstFreeIndex() {
      for (let i = 0; i < 10000; i++) {
        if (this.characters[i] == null)
          return i        
      }
      return -1
    }
  }
})
